@import '../../AppStyles.css';

.Main {
    width: 100%;
    > header {
        width: 100%;
        display: flex;
        justify-content: center;
    }
}

.main_companyInfo_container {
    opacity: 0;
    transform: translateY(100px); 
    transition: opacity 500ms ease, transform 500ms ease; 
    padding: 0px 15%;
    height: 400px;
    display: flex;
    width: 100%;
    margin-bottom: 30px;
    
    .main_companyInfo_text {
        width: 50%;
        text-align: center;
        align-content: center;
        
        h2 {
            color: var(--appMainColor);
            font-size: clamp(20px, 4vw, 40px);
            line-height: 80px;
        }
        
        p {
            font-size: clamp(12px, 2vw, 20px);
        }
    }
    
    .main_companyInfo_imgContainer {
        width: 50%;
        text-align: center;
        align-content: center;
    }
}

.main_photogallery_container {
    padding: 0 15%;
}

.main_companyInfo_container_animation {
    opacity: 1; 
    transform: translateY(0); 
}

@media (max-width: 480px) {
    .app_mainButton.header {
        padding: 5px 7px;
    }
}