.myLoader {
    display: inline-block;
    border: 3px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    border-top-color: var(--appColor2); 
    border-left-color: var(--appColor2); 
    width: 50px;
    height: 50px;
    animation: spin 1s linear infinite; 
    z-index: 1000;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
